import React from 'react';

import './roomRec.css';
import OptionsCard from './OptionsCard.js';

class RoomRec extends React.Component {


  render () {

    return (
      <div ref="stuffBehind" className = {`stuffBehind ${this.props.loggedIn ? '' : 'blurred'}`} >
        <OptionsCard/>
        <OptionsCard/>
        <OptionsCard/>
      </div>
    );

  }


}
export default RoomRec;



