import React from 'react';
import { BrowserRouter as  Route, BrowserRouter, NavLink} from 'react-router-dom';

import './index.css';
import AppName from './AppName';
import Banner from './Banner.js';
import RoomRec from './RoomRec.js';
import Transparent from './Transparent.js';


const Rooms = (props) => (

  <div id="homeLayout" >
    <div onClick={() => alert('fml')} className="topBar" >
        <NavLink to="/button/" className="navLink">
          <AppName />
        </NavLink>
    </div>
    <div>
      <Banner />
    </div>
    <RoomRec loggedIn={props.loggedIn}/>
     <Transparent loginClicked={props.login}/>
  </div>
);


export default Rooms;



